/**
 * Returns `True` if it's a mobile device.
 *
 * Based on:
 * http://stackoverflow.com/questions/11381673/detecting-a-mobile-browser
 * @return {Boolean}
 */
function isMobile(){
    return typeof window.orientation !== 'undefined';
}

module.exports = isMobile;



