import LZString from 'lz-string';

function Mindful() {}
Mindful.prototype.init = function(editor){
    this.editor     = editor;

    /*
     * Collect all the element we need to toggle UI states
     */
    this.nightMode = document.body.querySelector('.fk-icon-nightmode');
    this.info = document.body.querySelector('.fk-icon-info');
    this.showMode = document.body.querySelector('.fk-icon-hide');

    /*
     * Check if this is running as an extension
     */
    if(chrome && chrome.runtime.getManifest){
        this.manifest = chrome.runtime.getManifest();
        this.version = parseInt(this.manifest.version.replace(/\./g, ''));
        /*
         * syncs all changes within the editor
         */
        this.editor.on('history.checkpoint', this.sync.bind(this));
        /*
         * Loads the initial content upon opening a new tab
         */
        chrome.storage.sync.get('mindfulC', this.loadInitialContent.bind(this));
        /*
         * syncs changes across tabs and devices
         */
        chrome.storage.onChanged.addListener(this.updateTab.bind(this));
        /*
         * Make sure user sees length-overflow notification in every new tab…
         */
        localStorage.removeItem('nlength');
    }
    this.info.addEventListener('click', ()=> document.documentElement.classList.toggle('author'));

    // The migration (going from uncompressed to compressed content)
    // TODO: rewrite / remove in the future
    this.faultyUpdateContentRollBackOptionNotification();

    // link clicking + CMD
    this.initAnchorBehavior();

    // initialize night / day mode based on preferences
    this.bindNightDayModeListeners();
    this.setNightMode();
    this.setHideMode();
};


/**
 * createUpdateBackup creates a local backup on the user’s machine, whenever
 * the version number changes
 * @param  {String} content in HTML (not compressed!)
 */
Mindful.prototype.createUpdateBackup = function(content){
    let backupToken = `backup__mindful__${this.version}`;
    if(!localStorage.getItem(backupToken)){
        console.log('Created local backup for update', this.version);
        localStorage.setItem(backupToken, content);
    }
}

/**
 * loadInitialContent loads the initial content from Chrome API if available
 * @param  {Object} changes
 */
Mindful.prototype.loadInitialContent = function(result){
    if(result.mindfulC){
        var content = LZString.decompressFromUTF16(result.mindfulC);
        // create local backup with every version
        this.createUpdateBackup(content);
        // is updated from fauly buggy version
        // TODO: remove in the future
        localStorage.setItem('newVersion', 'true');
        // set content to editor
        this.editor.el.innerHTML = content;
    } else {
        console.log('mindfulC not found!');
        // Creates a backup from the old data schema (no compression)
        if(!localStorage.getItem('newVersion')){
            this.createNonCompressedBackup();
        }
    }
}
/**
 * createNonCompressedBackup
 */
Mindful.prototype.createNonCompressedBackup = function(){
    if(!localStorage.getItem('backup')){
        chrome.storage.sync.get('mindful', (result)=>{
            console.log('Setting editor to old model for the last time…');
            let content = result.mindful;
            if(content){
                localStorage.setItem('backup', content);
                this.editor.el.innerHTML = content;
                // saves the old editor content to the new content model
                this.sync();
            }
        });
    }
}

/**
 * updateTab updates the current tab and the local editor instance with
 * the current content
 * @param  {Object} changes
 * @param  {String} namespace
 */
Mindful.prototype.updateTab = function(changes, namespace){
    // old, uncompressed schema
    if(changes.mindful && document.hidden){
        localStorage.setItem('mindful', content);
        this.editor.el.innerHTML = changes.mindful.newValue;
    }
    // new, compressed schema
    if(changes.mindfulC && document.hidden){
        let c = LZString.decompressFromUTF16(changes.mindfulC.newValue);
        this.editor.el.innerHTML = c;
        this.editor.selection.restoreSelection();
        this.editor.selection.update();
        this.editor.selection.collapse(false);
    }
};


/**
 * sync compresses the string and syncs it with chrome sync api
 * @param  {Object} changes
 * @param  {String} namespace
 */
Mindful.prototype.sync = function(e){
    let str = LZString.compressToUTF16(this.editor.el.innerHTML);
    chrome.storage.sync.set({'mindfulC': str, 'v': this.version}, ()=>{
        if (chrome.runtime.error || chrome.runtime.lastError) {
            this.editor.emit('notify', {
                text: 'Sync warning: you have reached the maximum length we can sync over the free Chrome API’s. Please backup some of your older thoughts in your local notes.<br /><br />Thanks for using Mindful 👏',
                id: 'length'
            });
        }
    });
};


/*
 * TODO:
 * - faultyUpgrade logic darf nicht als standard angezeigt werden!!!
 * - notifications once pattern
 * - content overfill warning
 * - shortcut reminders
 * - new version delightful message
 */
Mindful.prototype.faultyUpdateContentRollBackOptionNotification = function(){
    setTimeout(()=>{
        if(localStorage.getItem('backup')){
            this.editor.emit('notify', {
                text: 'Mindful creates a local backup during <em>every</em> new update. If your notes aren’t showing up, you can restore them by clicking <a class="js-restore">here</a>.<br /><br />Thanks for using Mindful 🙌',
                button: 'GOT IT!',
                id: 'new1'
            });

            let jsRestore = document.querySelector('.js-restore');
            if(jsRestore){
                jsRestore.addEventListener('click', (el)=>{
                    let backup = localStorage.getItem('backup');
                    if(backup){
                       this.editor.el.innerHTML = backup;
                    }
                });
            }
        }
    }, 500);
};


Mindful.prototype.initAnchorBehavior = function(){
    var isCmd = false;
    var isFocused = false;

    this.editor.on('mousemove', (ev)=>{
        if(this.isFocused) document.documentElement.classList.remove('focus');
        this.isFocused = false;
    });

    this.editor.on('blur', (ev)=>{
        document.documentElement.classList.remove('metakey');
    });

    this.editor.on('keydown', (ev)=>{
        isCmd = (ev.metaKey || ev.ctrlKey);
        if(isCmd) document.documentElement.classList.add('metakey');

        if(!this.isFocused) document.documentElement.classList.add('focus');
        this.isFocused = true;
    });

    this.editor.on('click', (ev)=>{
        if((ev.metaKey || ev.ctrlKey) && ev.target.closest('a')){
            window.open(ev.target.closest('a').getAttribute('href'), '_blank');
        }
    });

    this.editor.on('keyup', (ev)=>{
        if(isCmd) document.documentElement.classList.remove('metakey');
        isCmd = false;
    });
};

Mindful.prototype.setNightMode = function(){
    document.documentElement.classList.remove('night', 'day');
    document.documentElement.classList.add(localStorage.getItem('daymode') || 'day');
};

Mindful.prototype.setHideMode = function(){
    document.documentElement.classList.remove('hide', 'show');
    document.documentElement.classList.add(localStorage.getItem('hidemode') || 'show');
};
Mindful.prototype.bindNightDayModeListeners = function(){
    this.nightMode.addEventListener('click', ()=>{
        localStorage.setItem('daymode', document.documentElement.classList.contains('night') ? 'day' : 'night');
        this.setNightMode();
    });
    this.showMode.addEventListener('click', ()=>{
        localStorage.setItem('hidemode', document.documentElement.classList.contains('show') ? 'hide' : 'show');
        this.setHideMode();
    });
};

module.exports = Mindful;



