var Vue = require('vue');
var styles = require('./fk-bubble-action.scss');


/*
    Takes a confirm function from its parent and calls it.
*/
Vue.component('fk-bubble-action', {
    props: [
        'label',
        'icon',
        'title',
        {
            name: 'click',
            type: Function,
            required: true
        }
    ],
    template: `
        <template v-if="icon">
            <div
                v-on="click:onClick"
                class="fk-bubble__action fk-bubble__action--icon fk-icon fk-icon-{{icon}}"
                title="{{label}}"></div>
        </template>
        <template v-if="!icon">
            <div
                v-on="click:onClick"
                title="{{title}}"
                class="fk-bubble__action"><content></content></div>
        </template>
    `,
    methods: {
        /**
        *   Calls the <bubble-confirm click="{{callback}}" function as
        *   defined in the `click` parent.
        */
        onClick: function(){
            this.click()
        }
    }
});



