var Vue = require('vue');
var styles = require('./fk-button.scss');
Vue.config.debug = true;

var BubbleButton = Vue.component('fk-button', {

    /**
     * Default properties
     * ==================
     */
    data(){
        return {
            /**
             * Determines whether the button's functionality is currently active
             */
            active: false,
            /**
             * Determines whether the button has focus (tabbable)
             */
            focused: false
        }
    },

    /**
     * Component properties
     * ====================
     */
    props: {
        button: {
            type: Object
        }
    },

    /**
     * Methods
     * =======
     */
    methods: {
        /**
         * Toggles a button state or expands it
         * @param {Event} $event
         * @return void
         */
        handleClick($event) {
            this.$parent.restoreSelection();
            if (this.button.type) {
                this.$parent.expand(this.button);
            } else {
                if (this.button.command) {
                    this.$parent.executeCommand(this.button);
                } else {
                    console.warn("%s button is missing a command property", this.button.title)
                }

            }

            $event.preventDefault();
        }

    },

    /**
     * Template
     * =======
     */
    template: `
        <div class="fk-button
                    fk-icon-{{button.icon}}"
            v-class="fk-button--active : button.active, fk-button--focused : button.focused"
            v-on="click:handleClick($event)"
            title="{{button.title}}">
        </div>
    `
});



