import Vue from 'vue';
import forEach from 'lodash/forEach';

// Require styles and child components
require('./fk-bubble.scss');
require('./fk-bubble-action');
require('./fk-button');
require('./fk-bubble-link-prompt');

/**
 * Bubble View
 */
let BubbleView = Vue.extend({

    /**
     * Component name
     * ==============
     */
    name: 'frontkit-bubble',

    /**
     * Default properties
     * ==================
     */
    data() {
        return {
            /**
             * Determines whether the bubble should be shown or not
             */
            isVisible: false,

            /**
             * First button to show in paginated view
             */
            startButton: 0,

            /**
             * Button with focus
             */
            buttonIndex: -1,

            /**
             * Maximum number of buttons to show in the paginated view
             */
            maxButtons: 5,

            /**
             * Indicates on which side the bubble would be cropped by the browser window,
             * if it were placed in the middle of the selection
             */
            croppedOn: false,

            /**
             * Reference to the button that is currently expanded
             */
            expandedButton: null,

            /**
             * List of available buttons
             */
            buttons: [],

            /**
             * Coordinates of the current selection
             */
            selectionCoords: {},

            /**
             * Coordinates of the current selection
             */
            isInTabbedMode: false,

            /**
             * Coordinates of the bubble
             */
            bubblePosition: {}
        }
    },

    /**
     * Observed properties
     * ==================
     */
    watch: {
        /**
         * Calculates and returns the position of the bubble (needs to be executed in
         * the next DOM update cycle, otherwise Firefox/Safari report incorrect dimensions
         * for the bubble width)
         * @return {Object} - The top/left coordinates of the bubble
         */
        '[selectionCoords.top, selectionCoords.left, expandedButton, startButton]'(val) {
            Vue.nextTick(() => {
                let [left, top] = [this.selectionCoords.left, this.selectionCoords.top];
                let bubbleEl = this.$$.bubble;
                let bubbleCoords = bubbleEl.getBoundingClientRect();

                // Arrow is placed in the middle of the bubble
                let arrowOffset = bubbleCoords.width / 2;

                // Adjust left offset
                left = this.selectionCoords.left - arrowOffset + this.selectionCoords.width / 2;

                // Further adjust left offset to make sure the bubble isn't cropped
                // on either side of the screen
                if (left < 0) {
                    this.croppedOn = 'left';
                    left = 0;
                } else if ((left + bubbleCoords.width) > window.innerWidth) {
                    this.croppedOn = 'right';
                    left = window.innerWidth - bubbleCoords.width;
                } else {
                    this.croppedOn = false;
                }

                // Adjust the top offset
                if(this.$options.editor.context.isMobile){
                    top = window.scrollY; //this.selectionCoords.top + this.selectionCoords.height + 10;
                } else {
                    top = this.selectionCoords.top - bubbleCoords.height - 10;
                }


                // We're using top+left instead of `transform: translate3d(…)` to position the
                // bubble to retain the possibility of adding a small transition with transform
                // when it appears/disappears
                this.bubblePosition = {
                    top: top + 'px',
                    left: left + 'px'
                };
            })
        }

    },

    /**
     * Computed properties
     * ===================
     */
    computed: {
        /**
         * Calculate position of the triangle (only when the bubble is on the edge of the browser's window)
         * @return {Object} - The translateX transform to apply to the arrow to place
         *                    it right in the middle of the selection
         */
        trianglePosition() {
            (this.expandedButton, this.startButton); // Trick Vue into watching these properties as well
            let bubbleEl = this.$$.bubble;
            if (this.croppedOn && bubbleEl) {
                let bubbleCoords = bubbleEl.getBoundingClientRect();
                let whereItShouldBe = this.selectionCoords.left + this.selectionCoords.width / 2;
                let whereItAlreadyIs = bubbleCoords.left + bubbleCoords.width / 2;
                let left = whereItShouldBe - whereItAlreadyIs;
                return {
                    transform: `translateX(${left}px)`
                }
            }
        },

        /**
         * List of classes that will be applied to the bubble with the `v-class` directive
         * @return {Object} - The map of classes that will be applied to the view
         */
        classes(){
            return {
                "fk-bubble": true,
                "fk-bubble--visible": this.isVisible,
                "fk-bubble--top": this.hasNativeBubble
            }
        },

        /**
         * Check if it is possible to go to the previous page
         * @return bool
         */
        hasPrev(){
            return this.startButton > 0;
        },

        /**
         * Check if there are more buttons to show
         * @return bool
         */
        hasNext(){
            return this.buttons.length > (this.startButton + this.maxButtons);
        }
    },

    /**
     * Methods
     * =======
     */
    methods: {
        /**
         * Display the next page of buttons
         */
        nextPage() {
            this.startButton = Math.min(
                this.buttons.length - this.maxButtons,
                this.startButton + this.maxButtons);
        },
        /**
         * Display the previous page of buttons
         */
        prevPage() {
            this.startButton = Math.max(0, this.startButton - this.maxButtons);
        },

        /**
         * Expand a button
         */
        expand(button) {
            if (button.onExpand) {
                this.expandButton(button);
            }
            this.expandedButton = button;
        },
        /**
         * Prevent mousedown events to keep selection in editor
         */
        preventMouseDown($event){
            // TODO: add generic class
            if (!/fk-bubble__link-prompt__input/.test($event.target.className)) {
                $event.preventDefault();
            }

        },
        /**
         *
         */
        restoreSelection() {
            return this.$options.plugin.restoreSelection();
        },
        /**
         *
         */
        saveSelection(){
            return this.$options.plugin.saveSelection();
        },
        /**
         *
         */
        expandButton(button){
            button.onExpand(this.$options.editor);
            return this.saveSelection();
        },

        toggleButton(buttonName){
            this.buttons.forEach((button)=> {
                if(button.name === buttonName){
                    if(this.expandedButton === button){
                        this.closeExpandedButton();
                    } else {
                        this.expand(button);
                    }
                }
            });
        },

        /**
         *  Gets the current button with focus
         */
        getFocusedButton(){
            return this.buttons.filter((b)=>{ return b.focused === true; })[0];
        },

        /**
         *  Update the focused button
         */
        updateIndex(index){
            this.buttonIndex = this.buttonIndex + index;
            this.buttonIndex = this.buttonIndex >= this.buttons.length ? 0 : this.buttonIndex;
            this.buttonIndex = this.buttonIndex < 0 ? this.buttons.length-1 : this.buttonIndex;

            this.buttons.forEach((button, i) => {
                button.focused = i === this.buttonIndex;
            });
        },


        /**
         *
         */
        getButtonValue(button){
            return button.getValue(this.$options.editor);
        },
        /**
         * Close the expanded button
         */
        closeExpandedButton(){
            if (this.expandedButton && this.expandedButton.onCollapse) {
                let value = this.getButtonValue(this.expandedButton);
                this.restoreSelection();
                this.expandedButton.onCollapse(this.$options.editor, value);
            }
            this.expandedButton = null;
        },
        /**
         * Execute a command in the context of the editor
         * @param {String|Function} command - The function or name of the command
         * @param {*} value - The value to pass to the command
         */
        executeCommand(button, value){
            let editor = this.$options.editor;
            // Place the selection back into the editor right before executing the command
            this.$options.plugin.restoreSelection();
            if (button.command.constructor === String) {
                editor.commands.execute.apply(editor.commands, button.command.split(':'), value);
            } else {
                button.command(editor, value);
            }
            this.expandedButton = null;
            this.saveSelection();
            this.$options.plugin.updateAvailableButtonStates(button);
        }
    },

    /**
     * Template
     * ========
     */
    template: `
        <div v-class="classes"
            v-style="bubblePosition"
            v-el="bubble"
            v-on="mousedown:preventMouseDown">

            <template v-if="!expandedButton">
                <div class="fk-bubble__pagination
                            fk-bubble__pagination--prev
                            fk-icon
                            fk-icon-left-dir"
                    v-if="hasPrev"
                    v-on="click:prevPage"></div>

                <fk-button v-repeat="button in buttons"></fk-button>

                <div class="fk-bubble__pagination
                            fk-bubble__pagination--next
                            fk-icon
                            fk-icon-right-dir"
                    v-if="hasNext"
                    v-on="click:nextPage"></div>
            </template>

            <template v-if="expandedButton">
                <div class="fk-bubble__pagination
                            fk-bubble__pagination--prev
                            fk-icon
                            fk-icon-left-dir"
                    v-on="click:closeExpandedButton(expandedButton)"></div>

                <component button="{{expandedButton}}"
                    is="{{expandedButton.type}}"></component>
            </template>

            <div class="fk-bubble__triangle"
                v-style="trianglePosition"></div>

        </div>
    `
});


export default BubbleView;



