var Vue = require('vue');
var styles = require('./fk-bubble-link-prompt.scss');

Vue.component('link-prompt', {

    /**
     * Properties
     * ==========
     */
    props: ['button'],

    /**
     * Methods
     * =======
     */
    methods: {
        confirm(){
            this.$parent.executeCommand(this.button, this.$$.input.value);
        },
        shortcut($event){
            if($event.keyCode === 8 && !this.$$.input.value.length){
                $event.preventDefault();
                return this.remove();
            }
            if(($event.metaKey || $event.ctrlKey ) && String.fromCharCode($event.keyCode) === 'K'){
                this.close();
            }
        },
        close(){
            this.$parent.closeExpandedButton();
        },
        remove(){
            this.$$.input.value = '';
            this.confirm();
        }
    },
    /**
     * Lifecycle methods
     * =================
     */
    created(){
        setTimeout(function() {
            let input = this.$$.input;
            let value = this.$parent.getButtonValue(this.button);
            input.focus();
            if(value){
                input.value = value;
                input.select();
            }
        }.bind(this), 200);
    },

    /**
     * Template
     * =======
     */
    template: `
        <div class="fk-bubble__link-prompt">

            <template v-if="button.datalist">
                <datalist id="fk-links">
                  <option v-repeat="button.datalist" value="{{value}}">{{text}}</option>
                </datalist>
            </template>

            <input autosuggest
                v-el="input"
                v-on="keyup:confirm | key 'enter',
                    keyup: close | key 'esc',
                    keydown: shortcut($event)
                    "
                list="fk-links"
                type="text"
                placeholder="Enter URL..."
                class="fk-bubble__link-prompt__input">

            <fk-bubble-action
                click="{{confirm}}"
                title="Confirm">OK</fk-bubble-action>

            <fk-bubble-action
                click="{{remove}}"
                icon="trash" title="Remove"></fk-bubble-action>
        </div>
    `,
});



