'use strict';

export default {
    'mod+a':        'selectAll',
    'mod+b':        'formatStrong',
    'mod+.':        'toggleCheckbox',
    'mod+i':        'emphasize',
    'mod+k':        'link',
    'mod+u':        'underline',
    'tab':          'indent',
    'shift+tab':    'outdent',
    'esc':          'escape'
};



